* {
    margin: 0;
    padding: 0px;
    box-sizing: border-box;
    scroll-behavior: smooth;
}

html,
body {
    min-height: 100vh;
    font-size: 100%;
    font-size: 14px;
}

.js-homework {
    background-image: url(../assets/fon_seryj_abstraktsiya_svetlyj_95691_1280x720.jpg);
    background-position: center center;
    background-size: cover;
    padding: 50px 30px 50px 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.nav {
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 50px;
}

a {
    text-decoration: none;
    text-transform: uppercase;
    font-size: 16px;
    color: #000000;
    border: #000000 solid 2px;
    padding: 5px;
    cursor: pointer;
    background-color: #9cdbfa;
    width: 250px;
    text-align: center;
    font-weight: bold;
}

a:hover {
    background-color: #b16ccc;
}

.js-module {
    width: 100%;
}

ul {
    margin-bottom: 50px;
}

ul li {
    list-style: none;
}

h2 {
    margin-bottom: 15px;
}

li {
    border: 2px solid #000000;
    margin-bottom: 2px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 65px;
    padding-left: 20px;
    padding-right: 20px;
}

button {
    height: 55px;
    width: 100px;
    background-color: #9cdbfa;
    cursor: pointer;
    padding: 2px;
    border: #000000 solid 2px;
}

button:hover {
    background-color: #b16ccc;
}